
          @import "@/styles/variables.scss";
          @import "@/styles/breakpoints.scss";
        

.project-container {
  & .project-wrapper {
    margin-bottom: 100px;
    &:last-child {
      margin-bottom: unset;
    }
  }
}

.project-wrapper {
  display: flex;
  height: auto;
  flex-direction: row;
  column-gap: 50px;
  @include tablet {
    flex-direction: column;
  }
  @include mobile {
    flex-direction: column;
  }

  &:nth-child(even) {
    flex-direction: row-reverse;
    @include tablet {
      flex-direction: column;
    }
    @include mobile {
      flex-direction: column;
    }
  }

  .project-contents {
    display: flex;
    flex-direction: column;
    color: var(--color-primary-text);
    width: 60%;
    @include tablet {
      width: 100%;
    }
    @include mobile {
      width: 100%;
    }

    .project-title {
      display: flex;
      align-items: center;
      column-gap: 10px;
      row-gap: 5px;
      flex-wrap: wrap;
      font-size: 20px;
      margin-bottom: 10px;
    }

    .project-duration {
      text-align: right;
      margin-bottom: 20px;
      font-size: 15px;
      color: var(--color-secondary-text);
      @include mobile {
        font-size: 14px;
      }
    }

    .project-description {
      margin-bottom: 20px;
      p {
        font-size: 16px;

        white-space: pre-line;
        word-break: keep-all;
        margin-bottom: 10px;
        @include mobile {
          font-size: 14px;
        }
      }
    }

    .project-hashtags {
      font-size: 15px;
      @include mobile {
        font-size: 12px;
      }
    }
  }

  .project-link_list {
    padding-left: 10px;
    font-size: 16px;
    @include mobile {
      font-size: 14px;
    }
    li {
      list-style-position: inside;
    }
  }

  .project-carousel {
    width: 40%;
    border-radius: 10px;
    overflow: hidden;
    align-self: center;
    @include tablet {
      width: 100%;
    }
    @include mobile {
      width: 100%;
    }
    img {
      aspect-ratio: 5 / 4;
      object-position: center;
      object-fit: contain;
    }
    video {
      width: 100%;
      height: 100%;
      aspect-ratio: 5 / 4;
      object-position: center;
      object-fit: contain;
    }
  }
}

.sub-project-container {
  margin-top: 100px;
  color: var(--color-primary-text);

  h2 {
    font-size: 22px;
    margin-bottom: 20px;
    @include mobile {
      font-size: 15px;
    }
  }

  ul {
    padding: 0;
    margin: 0;
    li {
      list-style: none;
      margin-bottom: 12px;
    }
  }

  .sub-project-main {
    font-size: 15px;
    word-break: keep-all;
    @include mobile {
      font-size: 12px;
    }
  }

  .sub-project-description {
    font-size: 14px;
    color: var(--color-secondary-text);
    @include mobile {
      font-size: 11px;
    }
  }
}
