
          @import "@/styles/variables.scss";
          @import "@/styles/breakpoints.scss";
        

.modal-enter-active,
.modal-leave-active {
  transition: opacity 100ms ease-in-out;
}
.modal-enter-from,
.modal-leave-to {
  opacity: 0 !important;
}

.modal-backdrop {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: var(--modal-backdrop-color);
  backdrop-filter: blur(3px);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 900;
  color: var(--color-primary-text);
}

.modal-contents {
  position: relative;
  width: 100px;
  height: auto;
  min-width: 320px;
  max-height: 90vh;
  padding: 20px;
  background-color: var(--modal-background-color);
  border-radius: 10px;
}

.close-button {
  position: absolute;
  right: 10px;
  top: 10px;
  color: var(--color-primary-text);
}
