
          @import "@/styles/variables.scss";
          @import "@/styles/breakpoints.scss";
        

#headLine {
  font-size: 4vw;
  letter-spacing: 2px;
  transition: opacity 100ms linear;
  transition: opacity 100ms linear, transform 100ms linear;
  color: var(--color-primary-text);
  margin-bottom: 20px;
}
#name {
  font-size: 5vw;
  font-weight: 800;
}
#keyword {
  color: var(--color-primary-text);
  margin-bottom: 10px;
}
#hashtags {
  color: var(--color-primary-text);
}
@include tablet {
  #headLine {
    font-size: 8vw;
  }
  #name {
    font-size: 9vw;
  }
}
@include mobile {
  #headLine {
    font-size: 12vw;
  }
  #name {
    font-size: 13vw;
  }
}
