
          @import "@/styles/variables.scss";
          @import "@/styles/breakpoints.scss";
        

.career-section-container {
  position: relative;
}

#line {
  position: absolute;
  left: 50%;
  right: 50%;
  translate: -50% 0;
  top: 0;
  bottom: 0;
  width: 2px;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--color-primary-text);
  background: linear-gradient(
    to bottom,
    var(--background-color) 0%,
    var(--color-primary-text) 10%,
    var(--color-primary-text) 90%,
    var(--background-color) 100%
  );
}

#timeline-wrapper {
  color: var(--color-primary-text);
  display: grid;
  grid-auto-flow: row;
  row-gap: 20px;
  max-width: $max-width;
  margin: 0 auto;
  grid-template-columns: 1fr;
  /* border: 1px dashed black; */
  padding: 60px 0;

  li {
    list-style: none;
    display: flex;
    flex-direction: column;
  }

  li:nth-child(odd) {
    justify-self: flex-end;
    margin-right: 50%;
    text-align: right;
    padding-right: 20px;
  }

  li:nth-child(even) {
    justify-self: flex-start;
    margin-left: 50%;
    text-align: left;
    padding-left: 20px;
  }
}

.career-title {
  font-size: 20px;
  font-weight: 600;
  @include mobile {
    font-size: 18px;
  }
}
.job-title {
  font-size: 16px;
  word-break: keep-all;
  @include mobile {
    font-size: 14px;
  }
}

.job-term {
  font-size: 14px;
  color: var(--color-secondary-text);
  @include mobile {
    font-size: 14px;
  }
}
