
          @import "@/styles/variables.scss";
          @import "@/styles/breakpoints.scss";
        

a,
button {
  appearance: none;
  text-decoration: none;
  color: var(--color-primary-text);
}

header {
  position: sticky;
  top: 0;
  width: 100%;
  height: $header-height;
  display: flex;
  place-items: center;
  flex-direction: column;
  padding: 0 20px;
  backdrop-filter: blur(10px);
  background-color: var(--header-background-color);
  z-index: 800;
  @include mobile {
    height: 60px;
  }
}

#logo {
  font-size: 32px;
  font-weight: 900;
}

nav {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  ol {
    height: 100%;
    display: grid;
    grid-auto-flow: column;
    list-style: none;
    gap: 25px;
    margin: 0;
    padding: 0;
    font-size: 16px;
    @include mobile {
      font-size: 14px;
      gap: 10px;
    }
    li {
      position: relative;
      color: var(--color-primary-text);
      :is(a, button) {
        display: flex;
        place-items: center;
        height: 100%;
      }
      &:before {
        content: "";
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
        width: 100%;
        height: 3px;
        background-color: var(--color-primary-text);
        transition: transform ease-in-out 100ms;
        transform: scaleX(0);
      }
      &:hover:before {
        transform: scaleX(1);
      }
    }
  }
}

.modal-button {
  padding: 0;
}
